.our-expertise {


  &__block {
    height: 1375px;
    background-color: #fff;
    border-radius: 30px;

    @include on-small-desktop {
      height: 759px;
    }

    &__wrapper {
      display: flex;
      flex-direction: column;

      @include on-small-desktop {
        flex-direction: row;
        justify-content: center;
        margin: 0 54px;
      }

      @include on-the-design {
        margin: 0 227px;
        justify-content: space-between;
      }

      @include on-large-screens {
        justify-content: space-between;
        margin: 0 756px;
      }
    }

    & > h2 {
      padding: 72px 20px 0;
    text-align: center;
    font-size: 44px;
    font-weight: 600;
    color: #253757;

  }

}

&__card {
  margin: 56px 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  @include on-desktop {
    width: 214px;
  }

  @include on-large-screens {
    width: 264px;
  }

  & > img {
    border-radius: 30px;
    width: 184px;
    height: 166;
    object-fit: cover;
    object-position: 55%;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }
  }

  & > h4 {
    margin: 32px 0 16px;
    font-weight: 600;
    font-size: 20px;
    color: #253757;
  }

  & > p {
    margin: 0 24px;
    color: #6C788B;
    font-family: Open-Sans, sans-serif;
    font-weight: 400;
    font-size: 14px;

    @include on-tablet {
      padding: 0 126px;
    }

    @include on-small-desktop {
      padding: 0;
      margin: 0;
    }
  }
}
}
